.contact-filter {
    font-size: 1.25em;
    text-align: center;
    label {
        margin-right: 10px;
    }
    input {
        height: 30px;
        width: 350px;
        border: 1px solid gray;
        border-radius: 0.2em;
        padding-left: 10px;
        
        &:focus{
            outline: none;
        }
    }
    }
    
    @media (max-width: 500px) {
        .contact-filter { 
            font-size: 1.6em;
            
            flex-grow: 1;
            input {
                width: 100%;
                height: 40px;
            }
        }
    }