.contact-preview-container {
    background-color: rgb(41, 41, 66);
    box-shadow: 0px 3px 2px #282835;
    margin: auto;
    margin-bottom: 5px;
    padding: 15px 60px;
    max-width: 500px;
    -webkit-column-break-inside: avoid;
    page-break-inside: avoid;
    break-inside: avoid;
  .contact-img {
    width: 70px;
    border-radius: 50%;
    background-color: white;
  }
  .contact-name {
    font-size: 1.6em;
    margin-left: 20px;
  }
  &:hover {
    cursor: pointer;
  }
}

@media (max-width: 600px) {
  .contact-preview-container {
    padding: 15px 40px;
    .contact-name {
      font-size: 1.4em;
    }
  }
}
